@import url('https://fonts.googleapis.com/css2?family=Source+Serif+Pro&display=swap');

.updateProfileContainer {
  width: 100vw;
  height: 100vh;
  max-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(231, 231, 231);
  position: fixed;
  top: 0%;
  left: 0;
}

.updateProfileBox {
  background-color: white;
  width: 25vw;
  height: 70vh;
  box-sizing: border-box;
  overflow: hidden;
}

.updateProfileHeading {
  text-align: center;
  font-family: 'Source Serif Pro', serif;
  font: 400 1.3vmax "serif";
  padding: 1.3vmax;
  border-bottom: 1px solid rgba(0, 0, 0, 0.205);
  width: 50%;
  margin: auto;
}

.updateProfileForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding: 2vmax;
  justify-content: space-evenly;
  height: 70%;
  transition: all 0.5s;
}

.updateProfileForm>div {
  display: flex;
  width: 100%;
  align-items: center;
}

.updateProfileForm>div>input {
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.267);
  border-radius: 4px;
  font: 300 0.9vmax;
  font-family: 'Source Serif Pro', serif;
  outline: none;
}


.updateProfileBtn {
  border: none;
  background-color: rgb(12, 159, 56);
  color: white;
  font: 300 0.9vmax "Roboto";
  font-family: 'Source Serif Pro', serif;
  width: 100%;
  padding: 0.8vmax;
  cursor: pointer;
  transition: all 0.5s;
  border-radius: 4px;
  outline: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}


.updateProfileBtn:hover {
  background-color: rgb(22, 118, 148);
}

@media screen and (max-width: 600px) {
  .updateProfileContainer {
    background-color: white;
  }

  .updateProfileBox {
    width: 100vw;
    height: 95vh;
    margin-top: 15rem;
  }

  .updateProfileForm {
    padding: 5vmax;
  }

  .icon {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 3vmax;
  }

  .updateProfileForm>div>input {
    padding: 2.5vmax 5vmax;
    font: 300 1.7vmax;
  }

  .updateProfileForm>div>i {
    font-size: 2.8vmax;
  }

  #updateProfileImage>img {
    width: 8vmax;
    border-radius: 100%;
  }

  #updateProfileImage>input::file-selector-button {
    height: 7vh;
    font: 400 1.8vmax;
  }

  .updateProfileBtn {
    font: 300 1.9vmax "Roboto";
    padding: 1.8vmax;
  }
  .updateProfileHeading {
    font-size: 1.5rem;
  }
}