:root {
    --color-white-100: #fff;
    --color-white-200: #f4f5f5;
    --color-white-300: #c9cccf;
    --color-white-400: #a1a6aa;
    --color-white-500: #798086;
    --color-black-100: #12161c;
    --color-black-200: #101419;
    --color-black-300: #0c0f13;
    --color-black-400: #0c0f13;
    --color-black-500: #080a0c;
    --color-red-100: #ffccd5;
    --color-red-200: #ffa3b4;
    --color-red-300: #ff6682;
    --color-red-400: #ff385d;
    --color-red-500: #ff002f;
    --shadow-small: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
    --shadow-medium: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
    --shadow-large: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}
*, *::before, *::after {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    list-style: none;
    list-style-type: none;
    text-decoration: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
}
html {
    font-size: 100%;
    box-sizing: inherit;
    scroll-behavior: smooth;
    height: -webkit-fill-available;
}
body {
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    height: -webkit-fill-available;
    color: var(--color-black-500);
    background-color: var(--color-white-100);
}
main {
    overflow: hidden;
}
a, button {
    cursor: pointer;
    user-select: none;
    border: none;
    outline: none;
    background: none;
    box-shadow: none;
    text-decoration: none;
}
img, video {
    display: block;
    max-width: 100%;
    height: auto;
    object-fit: cover;
}
.section {
    margin: 0 auto;
    padding: 5rem 0 1rem;
}
.container {
    max-width: 80rem;
    height: auto;
    margin: 0 auto;
    padding: 0 1.25rem;
}
.centered {
    text-align: center;
    vertical-align: middle;
    margin-bottom: 1rem;
}
.btn {
    display: inline-block;
    font-family: inherit;
    font-size: 1rem;
    font-weight: 500;
    line-height: 1.5;
    text-align: center;
    vertical-align: middle;
    white-space: nowrap;
    user-select: none;
    outline: none;
    border: none;
    border-radius: 0.25rem;
    text-transform: unset;
    transition: all 0.3s ease-in-out;
}
.btn-darken {
    padding: 0.75rem 2rem;
    color: var(--color-white-100);
    background-color: var(--color-black-200);
    box-shadow: var(--shadow-medium);
}
.btn-neutral {
    padding: 0.75rem 2rem;
    color: var(--color-black-500);
    background-color: var(--color-white-100);
    box-shadow: var(--shadow-medium);
}
.header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    height: auto;
    margin: 0 auto;
    background: radial-gradient(circle, rgba(37,44,82,1) 0%, rgba(50,3,12,1) 100%);
    box-shadow: var(--shadow-medium);
}
.navbar {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    align-items: center;
    align-content: center;
    height: auto;
    padding-top: 0.5rem;
}

@media only screen and (min-width: 900px) {
    .navbar {
        grid-template-columns: repeat(3, minmax(0, 1fr));
        width: 100%;
        height: 4.25rem;
        padding-top: 0;
        margin: 0 auto;
   }
}
.brand {
    justify-self: start;
    font-family: inherit;
    font-size: 1.6rem;
    font-weight: 700;
    line-height: 1.25;
    letter-spacing: -1px;
    text-transform: uppercase;
    color:rgb(12, 159, 56);
}
.search {
    grid-column: span 2;
    order: 3;
    width: 100%;
    height: auto;
    margin: 0 auto;
    padding-block: 1rem;
}
.search-form {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 100%;
    width: 100%;
    height: auto;
    padding-inline: 1.5rem;
    border-radius: 5px;
    background: var(--color-white-200);
}
.search-input {
    display: block;
    font-family: inherit;
    font-size: 1rem;
    font-weight: normal;
    line-height: inherit;
    width: 100%;
    height: 100%;
    padding-block: 0.75rem;
    border: none;
    outline: none;
    color: var(--color-black-500);
    background: transparent;
}
.search-submit {
    display: block;
    font-size: 1.25rem;
    line-height: 1.5;
    margin-top: 1px;
    background: transparent;
}
@media only screen and (min-width: 900px) {
    .search {
        justify-self: center;
        grid-column: initial;
        order: initial;
        width: 100%;
        height: auto;
   }
    .search-form {
        max-width: 30rem;
   }
}
.menu {
    position: fixed;
    top: -100%;
    left: 0;
    margin-right: -5rem !important;
    width: 100%;
    height: auto;
    overflow: hidden;
    padding-block: 3rem 2rem;
    background: radial-gradient(circle, rgba(37,44,82,1) 0%, rgba(50,3,12,1) 100%);
    box-shadow: var(--shadow-medium);
    transition: all 0.45s ease-in-out;
}
.menu-inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-left: 0rem !important;
    row-gap: 1.25rem;
}
.menu-link {
    font-family: inherit;
    font-size: 1rem;
    font-weight: 500;
    line-height: inherit;
    text-transform: uppercase;
    color: white;
    transition: all 0.35s ease-in-out;
}
.menu-link:hover {
    outline: none;
    color: rgb(12, 159, 56);
}
@media only screen and (min-width: 600px) {
    .menu {
        position: relative;
        justify-self: end;
        top: 0;
        /* left: inherit; */
        width: auto;
        height: auto;

        padding-block: 0;
        background: none;
        box-shadow: none;
        transition: none;
        margin-right: 0;
   }
    .menu-inner {
        display: flex;
        flex-direction: row;
        padding-left: 0rem !important;
        column-gap: 1.25rem;
        margin: 0 auto;
   }
    .menu-link {
        text-transform: capitalize;
   }
   
}
.burger {
    position: relative;
    justify-self: end;
    cursor: pointer;
    user-select: none;
    z-index: 10;
    width: 1.75rem;
    height: 1rem;
    border: none;
    outline: none;
    background: none;
    visibility: visible;
    transform: rotate(0deg);
    transition: 0.35s ease;
}
@media only screen and (min-width: 600px) {
    .burger {
        display: none;
        visibility: hidden;
   }
}
.burger-line {
    position: absolute;
    display: block;
    right: 0;
    width: 100%;
    height: 2px;
    border: none;
    outline: none;
    opacity: 1;
    transform: rotate(0deg);
    background-color: white;
    transition: 0.25s ease-in-out;
}
.burger-line:nth-child(1) {
    top: 0px;
}
.burger-line:nth-child(2) {
    top: 0.5rem;
}
.burger-line:nth-child(3) {
    top: 1rem;
}
.burger.is-active .burger-line:nth-child(1) {
    top: 0.5rem;
    transform: rotate(135deg);
}
.burger.is-active .burger-line:nth-child(2) {
    right: -1.5rem;
    opacity: 0;
    visibility: hidden;
}
.burger.is-active .burger-line:nth-child(3) {
    top: 0.5rem;
    transform: rotate(-135deg);
}
@media screen and (max-width: 600px) {
    .menu.is-active {
    top: 0;
    width: 100%;
    height: inherit;
}
}